import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Store, select } from '@ngrx/store';
import { setNode, removeNode, addNode, editNode } from '../../store/actions/hierarchy.action';
import { HierarchyService } from 'src/app/shared/components/hierarchy/hierarchy.service';
import { ApiService } from 'src/app/shared/service/api.service';
import { LocalDataService } from 'src/app/shared/service/local-data.service';
import { Route } from '@angular/compiler/src/core';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';


const FORM_TYPE_ADD = 'FORM_TYPE_ADD';
const FORM_TYPE_EDIT = 'FORM_TYPE_EDIT';

export interface IOrganization {
  id: string;
  name: string;
  description: string;
}

@Component({
  selector: 'app-organization',
  templateUrl: './organization.component.html',
  styleUrls: ['./organization.component.scss']
})
export class OrganizationComponent implements OnInit {

  private nodeInitialData = {
    name: 'Organization'
  };

  public node = this.nodeInitialData;
  public editNode = null;
  public addNode = null;
  public showEditModal = false;
  public entity = 'organization';
  // public modalTitle: string;

  // public form: FormGroup;
  private formType: string;

  public modalTitle: string;
  public form: FormGroup;
  public showModal = false;
  public organizations: any[];
  public staticOrganizations: IOrganization[] = [
    {
      id: '1',
      name: 'CRM',
      description: 'Lorem ipsum dolor sit amet consectetur, adipisicing'
    },
    {
      id: '2',
      name: 'Consumer Group',
      description: 'Lorem ipsum dolor sit amet consectetur, adipisicing'
    },
    {
      id: '3',
      name: 'HKT Premier',
      description: 'Lorem ipsum dolor sit amet consectetur, adipisicing'
    }
  ];

  constructor(
    private formBuilder: FormBuilder,
    private apiService: ApiService,
    private localDataService: LocalDataService,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.buildForm();

    if (environment.staticData) {
      this.organizations = this.staticOrganizations;
      return;
    }
    this.apiService.list('organization/roots', {
      limit: 999,
      offset: 0,
    }).subscribe( x => {
      this.organizations = x;
    });
  }

  showAppForm(type: string, org?: IOrganization): void {
    if (type === 'add') {
      this.modalTitle = 'New Organization';
    } else {
      this.modalTitle = `Edit ${org.name}`;
      this.form.setValue({
        description: org.description,
        name: org.name,
        id: org.id
      });
    }

    this.showModal = true;
  }

  closeModal(): void {
    this.form.reset();
    this.showModal = false;
  }

  submitForm(): void{
    this.form.markAllAsTouched();
    if (this.form.invalid) {
      return;
    }
    this.showModal = false;
    const {id, name, description}: IOrganization = this.form.value;

    if (id) {
      this.update({id, name, description});
    } else {
      this.create({ name, description});
    }

    this.form.reset();
  }

  private create({ name, description }): void{

    // TEMP
    if (environment.staticData) {
      this.organizations.push({
        id: new Date().getTime(),
        name,
        description
      });
      return;
    }

    this.apiService.create(this.entity, {
      description,
      name
    }).subscribe ( x => {
        this.organizations.push({
          id: x.id,
          name: x.name,
          description: x.description
        });
    });
  }

  private update({id, name, description}): void{
    // TEMP
    if (environment.staticData) {
      this.organizations.map( o => {
        if (id === o.id) {
          o.name = name;
          o.description = description;
        }
      });
      return;
    }

    this.organizations.map( x => {
      if (id === x.id) {
        x.name = name;
        x.description = description;
      }
    });
    this.apiService.update(this.entity, id, {
      description,
      name
    }).subscribe ( x => {
      this.organizations.map( o => {
        if (id === o.id) {
          o.name = name;
          o.description = description;
        }
      });
    });
  }

  deleteApp(app: IOrganization): void {
    const sure = confirm('Are you sure?');
    if (sure) {
      // TEMP
      if (environment.staticData) {
        this.organizations = this.organizations.filter( o => o.id !== app.id);
        return;
      }


      this.apiService.delete(this.entity, app.id).subscribe( x => {
        this.organizations = this.organizations.filter( o => o.id !== app.id);
      });
    }
  }

  private buildForm(): void {
    this.form = this.formBuilder.group({
      id: '',
      name: ['', [Validators.required]],
      description: ''
    });
  }

  selectOrg(org: IOrganization): void {
    this.localDataService.save('organization', org);
    this.router.navigate(['/organization/', org.id]);
  }
}
